#studentExam-component{
    font-family: Arial, Helvetica, sans-serif;
    background: linear-gradient(0deg, #a8edea, #fed6e3);
    height: 100vh;
    margin: 0;
    overflow: hidden;
  
    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  
    input[type="number"] {
      -moz-appearance: textfield;
    }
    
    .screen {
      display: flex;
      flex-direction: column;
    }
  
    .screen_header{
      display: flex;
      flex-direction: row;
      flex-wrap: nowrap;
    }
  
    .studentID_text{
      text-transform: uppercase;
      font-weight: 700;
      display: block;
      border: 1px solid #f1f1f1;
      width: 200px;
      margin: 8px 0 0 0;
      border-radius: 15px;
      background-color: white;
      padding: 8px 0 0 10px;
      color: #403866;
      box-shadow: 0 0 6px 1.5px #888888;
    }
  
    .screen_container {
      display: flex;
      flex-direction: row;
    }
  
    form {
      display: flex;
      border: 1px solid #f1f1f1;
      width: 25%;
      margin: 20px 0 0 20px;
      border-radius: 15px;
      background-color: white;
      box-shadow: 0 0 6px 1.5px #888888;
      height: 40%;
      max-height: 530px;
    }
    h1 {
      color: #403866;
    }
  
    .errorText {
      font-size: smaller;
      font-style: italic;
      width: 90%;
      text-align: center;
      color: red;
      margin: 0 auto;
      margin-top: 10px;
    }
  
    .noticeText{
      padding-top: 7px;
      font-size: smaller;
      font-style: italic;
      width: 50%;
      color: red;
      margin-right: 10%;
    }

    input {
      transition: all 0.3s ease 0s;
      text-transform: uppercase;
      display: block;
      border-radius: 23.5px;
      height: 47px;
      padding: 0 24px;
      color: #808080;
      font-size: 13px;
      border: none;
      background: #f2f2f2;
      outline: none;
      margin: 8px;
    }
  
    .back-button {
      text-transform: uppercase;
      margin: 8px 18.3% 0 20px;
      transition: all 0.35s ease 0s;
      width: 6%;
      border-radius: 20px;
      background-color: #6dabe4;
      box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.2);
      border: none;
      color: white;
      cursor: pointer;
      outline: none;
      text-align: center;
      padding: 6.5px 13px;
      font-size: smaller;
      &:hover {
        background-color: #2ee59d;
        box-shadow: 0px 8px 13px rgba(46, 229, 157, 0.3);
        color: #fff;
        width: 8.5%;
        text-align: center;
        margin-right: 15.8%;
      }
    }
  
    .button-add {
      text-transform: uppercase;
      box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.2);
      transition: all 0.35s ease 0s;
      border-radius: 20px;
      outline: none;
      background-color: #6dabe4;
      padding: 14px 20px;
      margin: 30px auto;
      border: none;
      cursor: pointer;
      width: 40%;
      color: white;
      &:hover {
        background-color: #2ee59d;
        box-shadow: 0px 10px 22px rgba(46, 229, 157, 0.3);
        color: #fff;
        transform: translateY(-7px);
      }
    }
    .button-reset{
      margin: 20px auto;
      margin-right: 3%;
      padding: 14px 20px;
      text-transform: uppercase;
      box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
      transition: all 0.35s ease 0s;
      background-color: #6dabe4;
      border: none;
      cursor: pointer;
      width: 20%;
      color: white;
      border-radius: 20px;
      outline: none;
      &:hover {
        background-color: #2ee59d;
        box-shadow: 0px 7px 22px rgba(46, 229, 157, 0.3);
        color: #fff;
        transform: translateY(-7px);
      }
    }
    .button-submit {
      text-transform: uppercase;
      box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
      transition: all 0.35s ease 0s;
      background-color: #6dabe4;
      padding: 14px 20px;
      margin: 20px auto;
      border: none;
      cursor: pointer;
      width: 20%;
      color: white;
      border-radius: 20px;
      outline: none;
      &:hover {
        background-color: #2ee59d;
        box-shadow: 0px 7px 22px rgba(46, 229, 157, 0.3);
        color: #fff;
        transform: translateY(-7px);
      }
    }
  
    .container {
      display: flex;
      flex-direction: column;
      padding: 10px;
      width: 100%;
    }
    .label_flex {
      display: flex;
      flex-wrap: nowrap;
      flex-direction: column;
      & > label {
        width: 33.333333333333%;
        margin: 10px auto;
        text-align: center;
        line-height: 75px;
        font-size: 20px;
      }
    }
    .input_flex {
      display: flex;
      flex-direction: column;
    }
    .suggestion_flex {
      display: flex;
      flex-wrap: nowrap;
      flex-direction: column;
      width: 100%;
      margin: 0 auto;
      max-height: 150px;
      & > button {
        color: white;
        margin: 5px auto;
        text-align: center;
        line-height: 38px;
        box-sizing: border-box;
        background-color: #6dabe4;
        border: none;
        border-radius: 20px;
        transition: all 0.4s ease 0s;
        width: 85%;
        padding: 0 3%;
        outline: none;
        &:hover {
          color: white;
          margin: 5px auto;
          text-align: center;
          box-sizing: border-box;
          border: none;
          border-radius: 20px;
          width: 90%;
          padding: 0 5%;
          background-color: #2ee59d;
          outline: none;
        }
      }
    }
    ::-webkit-scrollbar {
      display: none;
    }
    .table_flex {
      margin: 20px 3% 0 3%;
      width: 75%;
      .table-footer{
        display: flex;
        flex-direction: row;
      }
    }
    .table {
      box-shadow: 0 0 6px 1.5px #888888;
      width: 100%;
      // height: 100vh;
      display: flex;
      flex-wrap: wrap;
      background-color: white;
      margin: 0;
      font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
        "Helvetica Neue", Arial, sans-serif;
      font-size: 19px;
      font-weight: 400;
      line-height: 1.5;
      border-radius: 13px;
      .table-body-scroll{
        max-height: 70vh;
        overflow: scroll;
        width: 100%;
        .table-body {
          display: flex;
          width: 100%;
          color: #808080;
          flex-wrap: wrap;
          overflow: scroll;
        }
      }
      .table-header {
        background-color: #6dabe4;
        color: white;
        width: 100%;
        border-top-left-radius: 13px;
        border-top-right-radius: 13px;
        font-weight: bold;
      }
      .row {
        display: inline-flex;
        width: 100%;
        box-sizing: border-box;
        justify-content: center;
        align-items: center;
        border-bottom: 1px solid #999;
        // height: 45px;
        max-height: 60px;
      }
      .column {
        padding: 10px 7px 10px 7px;
        text-align: center;
        &:nth-child(1) {
          width: 12%;
        }
        &:nth-child(2) {
          width: 49%;
        }
        &:nth-child(3) {
          width: 8%;
        }
        &:nth-child(4) {
          width: 5%;
        }
        &:nth-child(5) {
          width: 8%;
        }
        &:nth-child(6) {
          width: 10%;
        }
      }
      .icon {
        background: transparent;
        width: 35px;
        height: 35px;
        margin: 0px;
        padding: 0px;
        font-size: 20px;
        color: #808080;
        border: none;
        outline: none;
        cursor: pointer;
      }
    }
    .error {
      color: red;
      .icon {
        color: red;
      }
    }
    .table-body {
      .row {
        &:last-child {
          border: none;
        }
      }
      .column {
        &:nth-child(1) {
          border-right: 1px dashed #999;
        }
        &:nth-child(2) {
          width: 49%;
        }
        &:nth-child(3) {
          border-left: 1px dashed #999;
          border-right: 1px dashed #999;
          width: 8%;
        }
        &:nth-child(4) {
          border-right: 1px dashed #999;
          width: 5%;
        }
        &:nth-child(5) {
          border-right: 1px dashed #999;
          width: 8%;
        }
        &:nth-child(6) {
          width: 10%;
        }
      }
    }
  }
  